function activeBtn(modal, col) {
    var input = $('' + modal + ' input');
    var btn = $('' + modal + ' .btn');

    function proverka() {
        var inputValid = $('' + modal + ' input.valid').length;
        var inputError = $('' + modal + ' input.error').length;
        if (inputError == 0) {
            if (input.hasClass('valid')) {
                if (inputValid >= col) {
                    btn.removeClass('btn-disabled');
                }
            }
        } else {
            btn.addClass('btn-disabled');
        }
    }
    input.on('keyup', function () {
        proverka();
    });
    input.on('blur',function () {
        proverka();
    });
    input.on('click', function () {
        proverka();
    });
}

function valid(form) {
    $.validator.addMethod("minlenghtphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 10;
        },
        "");
    $.validator.addMethod("requiredphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 1;
        },
        "");
    $(form).validate({
        rules: {
            name: {
                required: true,
                minlength: 2
            },
            email: {
                required: true,
                email: true
            },
            password: {
                required: true,
                minlength: 3
            },
            phone: {
                requiredphone: true,
                minlenghtphone: true
            },
            check: {
                required: true
            },
            address: {
                required: true,
                minlength: 5
            }
        },
        messages: {
            name: {
                required: ''
            },
            phone: {
                required: ''
            },
            terms: {
                required: ''
            }
        },
        errorPlacement: function(error, element) {},
        highlight: function(element, errorClass, validClass) {
            $(element).addClass(errorClass).removeClass(validClass);
        },
        unhighlight: function(element, errorClass, validClass) {
            $(element).removeClass(errorClass).addClass(validClass);
        },
        submitHandler: function submitHandler(form) {
            //alert("Submitted!");
            $.ajax({
                type: $(form).attr('method'),
                url: $(form).attr('action'),
                data: $(form).serialize(),
                success: function (data) {
                    if(parseInt(data) == 1) {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    } else {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    }
                },
                error: function () {
                    $(form).removeClass('form--active');
                    $(form).siblings('.form__ok').addClass('form--active');
                }
            });

            return false;
        }
    });
}

$('.modal__pas').on('click', function () {
    var input = $(this).prev('input');
   var type = input.attr('type') == "text" ? "password" : "text";
   $(this).toggleClass('modal__pas--active');
   input.prop('type', type);
});

